import React from "react";
import { Helmet } from "react-helmet";
import { graphql } from "gatsby";
import styled from "styled-components";

import { BlogWrapper } from "../common";
import { rhythm } from "../../../utils";
import { fontColor } from "../../../styles";

export default function BlogPostTemplate({ data }) {
  const { markdownRemark: post } = data;
  const { frontmatter, html, timeToRead } = post;
  const { title, date } = frontmatter;
  return (
    <BlogWrapper>
      <Helmet title={title} />
      <BlogArticle>
        <BlogHeader>
          <BlogTitle>{title}</BlogTitle>
          <small>
            Last updated: {date} â€¢ {timeToRead} min read
          </small>
        </BlogHeader>
        <BlogBody dangerouslySetInnerHTML={{ __html: html }} />
      </BlogArticle>
    </BlogWrapper>
  );
}

const BlogArticle = styled.main`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const BlogHeader = styled.div`
  padding: ${rhythm(1)} 0;
  width: 100%;
  text-align: center;
  small {
    opacity: 0.8;
    font-size: ${rhythm(0.75)};
  }
`;

const BlogTitle = styled.h1`
  font-size: ${rhythm(2.5)};
  color: ${fontColor};
  font-weight: 700;
`;

const BlogBody = styled.article`
  font-size: ${rhythm(0.75)};
  code {
    font-size: ${rhythm(0.75)};
    
  }
  pre {
    &.language-{javascript} {
      background: #282c34;
      border-radius: 6px;
      color: #abb2bf;
      overflow: auto;
      padding: 28px 36px;
      line-height: 1.6;
    }
  }
`;

export const query = graphql`
  query blogPostByPath($path: String!) {
    markdownRemark(frontmatter: { type: { eq: "blog" }, path: { eq: $path } }) {
      id
      frontmatter {
        title
        type
        date(formatString: "MMM DD, YYYY")
      }
      timeToRead
      html
    }
  }
`;
